import { useRef,useEffect,useState} from 'react';
import axios from "axios";
import "./Register.css"
import { useNavigate, Link } from "react-router-dom";
import { useDispatch } from "react-redux";
import { loginCall } from "../../actionCalls";

export default function Register() {
  const dispatch = useDispatch();
  const username = useRef();
  const password = useRef();
  const passwordConfirm = useRef();
  const navigate = useNavigate();
  const [windowWidth, setWindowWidth] = useState(window.innerWidth);

    useEffect(() => {
        window.addEventListener("resize", () => setWindowWidth(window.innerWidth));

        return () => {
            window.removeEventListener("resize", () => setWindowWidth(window.innerWidth));
        };
    }, []);

  const handleRegister = async (e) => {
    e.preventDefault();
    if (username.current.value.length < 4 || username.current.value.length > 15) {
      username.current.setCustomValidity("415");
      username.current.reportValidity();
      return;
    }

    if (password.current.value.length < 6 || password.current.value.length > 50) {
      password.current.setCustomValidity("6");
      password.current.reportValidity();
      return;
    }

    if (password.current.value !== passwordConfirm.current.value) {
      passwordConfirm.current.setCustomValidity("");
      passwordConfirm.current.reportValidity();
      return;
    }

    try {
      const res = await axios.get(`/auth?username=${username.current.value}`);
      if (res.data.length) {
        username.current.setCustomValidity("");
        username.current.reportValidity();
        return;
      }
    } catch (err) {
      console.log(err);
      navigate("/error", { state: { message: "" } });
    }

    try {
      const user = {
        username: username.current.value,
        password: password.current.value,
      }
      await axios.post("/auth/register", user);
      const res = await axios.post("auth/login", user,{ withCredentials: true });
      await loginCall(res.data, dispatch);
      window.location.reload();
    } catch (err) {
      console.log(err);
      navigate("/error", { state: { message: "" } });
    }
    
    
  };

  return (
    <div className='Register'>
      <div className="RegisterContainer">
        <div className="RegisterLeft">
          <div className="RegisterLeftContainer">
            <h1 className='RegisterLeftTitle'>Mini Diary</h1>
            <div className="RegisterLeftDesc"></div>
          </div>
        </div>
        <div className="RegisterLight" >
            <form className='RegisterLightForm' action="" onSubmit={handleRegister}>
              <h4 className='RegisterLightTop'></h4>
              <input className='RegisterLightUsername' type="text" placeholder=''  ref={username} required onInput={(e) => e.target.setCustomValidity("")} />
              <input className='RegisterLightPassword' type="password" placeholder='' ref={password} required onInput={(e) => e.target.setCustomValidity("")}/>
              <input className='RegisterLightConfirmPassword' type="password" placeholder='' required ref={passwordConfirm} onInput={(e) => e.target.setCustomValidity("")}/>        
              <button className='RegisterLightSignup' type="submit"></button>
              <Link to="/login" className='RegisterLightLogin'>
                <button className='RegisterLightLoginButton' type="button"></button>
              </Link>
            </form>
        </div>
      </div>
    </div>
  )
}
