import React, { useEffect } from 'react'
import ReactDom from "react-dom"
import "./Delete.css";

export default function Delete({ handleDelete, setIsDeleting, comment,  }) {  
    useEffect(() => {
        document.body.style.overflow = "hidden";

        return () => {
            document.body.style.overflow = "";
        };
    }, []);

    return (
        ReactDom.createPortal(
            <>
                <div className="DeleteContainer">
                    <div className="DeleteDialog">
                        <small className="DeleteDialogTop">{comment ? "":""}</small>
                        <div className="DeleteDialogButtons">
                            <div onClick={() => setIsDeleting(false)} className='DeleteDialogLeftButton'><small></small></div>
                            <div onClick={handleDelete} className='DeleteDialogRightButton' ><small></small></div>
                        </div>
                    </div>
                </div>
            </>
            ,
            document.getElementById("modal-root")
        )
    );
}
